import React from 'react';

import Card from '../Components/Card';

import StackOverflow from '../assets/images/StackOverflow.PNG';
import imagereact from '../assets/images/imagereact.PNG';
import Blockchain from '../assets/images/Blockchain.PNG';
import Huffman from '../assets/images/huffman.jpg';
import Arihant from '../assets/images/Arihant.PNG';
import covid from '../assets/images/covid.jpeg';
import Home1 from '../assets/images/Home1.PNG';
import HitsPagerank from '../assets/images/pagerank.png';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

class Carousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Stackoverflow Analysis',
                    subTitle: 'AWS, GCP, Spark, SQL, Python',
                    imgSrc: StackOverflow,
                    link: 'https://drive.google.com/file/d/1vavjtgukm76E3Wod8WVTM3Ha8WEnfV8H/view?usp=sharing',
                    selected: false
                },
                {
                    id: 1,
                    title: 'Covid-19 Tracker',
                    subTitle: 'ReactJS, React Router, React-Chart, AWS-Amplify',
                    imgSrc: covid,
                    link:'https://master.d2mi63cmawfs1z.amplifyapp.com/',
                    selected: false
                },
                {
                    id: 2,
                    title: 'Portfolio (This)',
                    subTitle: 'ReactJS, React Router, React-Spring, Express-Node, SMTP',
                    imgSrc: imagereact,
                    selected: false

                },
                {
                    id: 3,
                    title: 'Internet Speed Test (Empirix)',
                    subTitle: 'Javascript, Java, HTML/CSS, WebSocket, Gradle, Node.Js, Spring',
                    imgSrc: Home1,
                    selected: false

                },
                {
                    id: 4,
                    title: 'Bitcoin Deanonymization',
                    subTitle: 'Java, Python, GCP, Bitcoin core, Hadoop',
                    imgSrc: Blockchain,
                    link: 'https://docs.google.com/presentation/d/1Rq1XZ0XhbG53KCvuDaQ6_MI3hWYDbdAOJrVWaZb4UuM/edit?usp=sharing',
                    selected: false
                },
                {
                    id: 5,
                    title: 'Huffman Algorithm',
                    subTitle: 'Java',
                    imgSrc: Huffman,
                    link: 'https://github.com/dd399/Huffman-Coding',
                    selected: false
                },
                {
                    id: 6,
                    title: 'Arihant AI Website',
                    subTitle: 'Node.js, Moment.js, PostgresSQL, Bootstrap',
                    imgSrc: Arihant,
                    link: 'http://arihantai.com/',
                    selected: false
                },
                {
                    id: 7,
                    title: 'Hits-Pagrank Algorithm',
                    subTitle: 'Java, Python',
                    imgSrc: HitsPagerank,
                    link: 'https://github.com/dd399/Hits-and-pagerank-',
                    selected: false
                },
            ]
        }
    }

    


    handleCardClick = (id, card) => {

        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }


    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel;