import React from "react";
import CountUp from "react-countup";
// import Card from '../Components/Card'
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  CardContent,
  Typography,
  CardMedia,
  CardActionArea,
  CardActions,
} from "@material-ui/core";
import programming from "../assets/images/code.jpg";
import cert from "../assets/images/cert.png";
import work from "../assets/images/work.png";
import award from "../assets/images/award.jpg";
import { Button } from "react-bootstrap";
import styles from "./Education.module.css";

const useStyles = makeStyles({
  root: {
    marginTop: 100,
    marginLeft: 125,
    marginBottom: 120,
    width: 220,
    height: 400,
    // borderRadius: 12,
    padding: 6,
  },
  hoveredStyle: {
    cursor: "default",
  },
  media: {
    height: 195,

    // borderRadius: 6,
  },
});

function EducationPage() {
  const classes = useStyles();
  return (
    <div>
      <div className="row" style={{ cursor: "initial" }}>
        <Card className={classes.root} style={{ cursor: "initial" }}>
          <CardActionArea style={{ cursor: "initial" }}>
            <CardMedia
              style={{ cursor: "initial" }}
              className={classes.media}
              image={programming}
              title="Finished Project"
            />
            <CardContent style={{ borderstyle: "solid", bordercolor: "coral" }}>
              {/* <Typography gutterBottom variant="body2" component="h5"> */}
              <h3> Finished Projects:</h3>
              <h2
                style={{
                  borderWidth: "thick",
                  textAlign: "center",
                  borderStyle: "solid",
                  borderColor: "skyblue",
                  color: "green",
                }}
              >
                <CountUp
                  start={0}
                  end={19}
                  delay={0}
                  duration={7}
                  separator=","
                />
              </h2>

              {/* </Typography> */}
            </CardContent>
          </CardActionArea>
        </Card>
        <Card className={classes.root}>
          <CardActionArea>
            <CardMedia
              style={{ cursor: "initial" }}
              className={classes.media}
              image={cert}
              title="Certfications"
            />
            <CardContent>
              <h3> Certifications:</h3>
              <h2
                style={{
                  borderWidth: "thick",
                  textAlign: "center",
                  borderStyle: "solid",
                  borderColor: "skyblue",
                  color: "green",
                }}
              >
                <CountUp
                  start={0}
                  end={12}
                  delay={0}
                  duration={7}
                  separator=","
                />
              </h2>
            </CardContent>
          </CardActionArea>
          <CardActions></CardActions>
        </Card>
        <Card className={classes.root}>
          <CardActionArea>
            <CardMedia
              style={{ cursor: "initial" }}
              className={classes.media}
              image={work}
              title="work ex"
            />
            <CardContent>
              <h3> Work Ex:</h3>
              <h2
                style={{
                  borderWidth: "thick",
                  textAlign: "center",
                  borderStyle: "solid",
                  borderColor: "skyblue",
                  color: "green",
                }}
              >
                <CountUp
                  start={0}
                  end={2}
                  delay={0}
                  duration={7}
                  separator=","
                />
              </h2>
              <h3>years</h3>
            </CardContent>
          </CardActionArea>
        </Card>
        <Card className={classes.root}>
          <CardActionArea>
            <CardMedia
              style={{ cursor: "initial" }}
              className={classes.media}
              image={award}
              title="Achievements"
            />
            <CardContent>
              <h3> Achievements:</h3>
              <h2
                style={{
                  borderWidth: "thick",
                  textAlign: "center",
                  borderStyle: "solid",
                  borderColor: "skyblue",
                  color: "green",
                }}
              >
                <CountUp
                  start={0}
                  end={3}
                  delay={0}
                  duration={7}
                  separator=","
                />
              </h2>
            </CardContent>
          </CardActionArea>
        </Card>
      </div>
      <div className={styles.container}>
        <Button
          style={{ margin: "5px", padding: "10px" }}
          href="https://gitconnected.com/dd399"
          size="small"
        >
          <b> Explore</b>
        </Button>

        <Button
          style={{ margin: "5px", padding: "10px" }}
          href="https://drive.google.com/file/d/1CperoR1ATKrjQF0soNWvP6rkskBav-aX/view?usp=sharing"
          size="small"
        >
          <b> Resume</b>
        </Button>
      </div>
    </div>
  );
}

export default EducationPage;
