import React from 'react';
import Container from 'react-bootstrap/Container';
import { Row, Col } from 'react-bootstrap';
import {SocialIcon} from 'react-social-icons'
import { MDBContainer} from "mdbreact";
import '../Footer.css';
function Footer(){
    return(
        <div id="Footer">
        <footer className= "mt-5">
            <Container fluid={true}>
                <Row className="border-top justify-content-between p-0">
                    <Col className="p-3" md={3} sm={10} >
                        <h6>Dhwanil Desai</h6>
                    </Col>
                    <Col className = "p-3 d-flex-justify-content-center" md={4.5} >
                    <SocialIcon url = "http://linkedin.com/in/dhwanil-desai" />
                    <SocialIcon url = "http://github.com/dd399" />
                    <SocialIcon url = "https://www.youtube.com/playlist?list=PLQzIepe7IVsWiOuwNPgw-vTOxdKCjP_XS" />
                    <SocialIcon url = "https://gitconnected.com/dd399" />
                    </Col>
                    <Col className="p-3 d-flex justify-content-end" md={3}>
                    <MDBContainer>
                    <h6>&copy; {new Date().getFullYear()} Copyright: Dhwanil Desai</h6>
                    </MDBContainer>
                    </Col>
                </Row>
            </Container>

        </footer>
        </div>
    )
}

export default Footer;