import React from "react";
import "../HomePage.css";
import dd1 from "../assets/images/dd11.jpeg";
import dd2 from "../assets/images/dd1.jpeg";

function HomePage(props) {
  document.addEventListener("DOMContentLoaded", function () {
    let wrapper = document.getElementById("wrapper");
    let topLayer = wrapper.querySelector(".top");
    let handle = wrapper.querySelector(".handle");
    let skew = 0;
    let delta = 0;

    if (wrapper.className.indexOf("skewed") !== -1) {
      skew = 1000;
    }

    wrapper.addEventListener("mousemove", function (e) {
      delta = (e.clientX - window.innerWidth / 2) * 0.5;

      handle.style.left = e.clientX + delta + "px";

      topLayer.style.width = e.clientX + skew + delta + "px";
    });
  });
  return (
    <div>
      <section id="wrapper" class="skewed">
        <div class="layer bottom">
          <div class="content-wrap">
            <div class="content-body">
              <h1>&lt;Coder&gt;</h1>
              <p>
               <h3>“I am committed to push my branch to the master”</h3>
              </p>
            </div>
            <img src={dd1} alt="" />
          </div>
        </div>

        <div class="layer top">
          <div class="content-wrap">
            <div class="content-body">
              <h1>&#123;Life&#125;</h1>
              <p>
              <h3>“I'm not a great programmer; I'm just a good programmer with great habits.”</h3>
              </p>
            </div>
            <img src={dd2} alt="" />
          </div>
        </div>

        <div class="handle"></div>
      </section>
    </div>
  );
}

export default HomePage;