import React from "react";

import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Navbar";
import "bootstrap/dist/css/bootstrap.min.css";

import "./App.css";
import Footer from "./Components/Footer";
import HomePage from "./Pages/HomePage";
import ProjectPage from "./Pages/ProjectPage";
import ContactPage from "./Pages/ContactPage";
import AboutPage from "./Pages/AboutPage";
import EducationPage from "./Pages/EducationPage";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "Dhwanil Desai",
      headerLinks: [
        { title: "Home", path: "/" },
        { title: "About", path: "/about" },
        { title: "Stats", path: "/stats" },
        { title: "Projects", path: "/projects" },

        { title: "Contact", path: "/contact" },
      ],

      home: {
        title: "Keep Exploring",
        subTitle: "Trying to make a difference by every day",
        text: "Checkout my projects for sure, go to Projects page",
      },

      project: {
        title: "Fun Projects",
        subTitle: "Made difference in my life",
      },

      about: {
        title: "About me",
      },

      contact: {
        title: "Let's Talk",
      },

      education: {
        title: "Journey Till Now",
      },
    };
  }
  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>
              <b>
                <font size="+1">Dhwanil Desai</font>
              </b>
            </Navbar.Brand>
            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">
                  <b>
                    <font size="+1">Home</font>
                  </b>
                </Link>
                <Link className="nav-link" to="/about">
                  <b>
                    <font size="+1">About</font>
                  </b>
                </Link>
                <Link className="nav-link" to="/stats">
                  <b>
                    <font size="+1">Stats</font>
                  </b>
                </Link>
                <Link className="nav-link" to="/projects">
                  <b>
                    <font size="+1"> Projects</font>
                  </b>
                </Link>

                <Link className="nav-link" to="/contact">
                  <b>
                    <font size="+1">Contact</font>
                  </b>
                </Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Route
            path="/"
            exact
            render={() => (
              <HomePage
                title={this.state.home.title}
                subTitle={this.state.home.subTitle}
                text={this.state.home.text}
              />
            )}
          />
          <Route
            path="/stats"
            exact
            render={() => <EducationPage title={this.state.education.title} />}
          />
          <Route
            path="/projects"
            exact
            render={() => (
              <ProjectPage
                title={this.state.project.title}
                subTitle={this.state.project.subTitle}
              />
            )}
          />
          <Route
            path="/about"
            exact
            render={() => <AboutPage title={this.state.about.title} />}
          />
          <Route
            path="/contact"
            exact
            render={() => <ContactPage title={this.state.contact.title} />}
          />

          <Footer />
        </Container>
      </Router>
    );
  }
}

export default App;
