import React from "react";
import Hero from "../Components/Hero";
import "../About.css";
function AboutPage(props) {
  return (
    <body>
      <Hero title={props.title}></Hero>

      <div className="content" fluid="true">
        <div className="slider-wrapper" fluid="true">
          I am a
          <div className="slider" fluid="true">
            <div className="slider-text1" fluid="true">
              Software Engineer
            </div>

            <div class="slider-text2" fluid="true">
              Front-end Developer
            </div>

            <div class="slider-text3" fluid="true">
              Back-end Developer
            </div>
          </div>
        </div>
      </div>
      
      <div class="cards">
        <div class="card">
          <div class="skills">
            <ul class="lines">
              <li class="line l--0">
                <span class="line__label title">Level:</span>
              </li>
              <li class="line l--25">
                <span class="line__label">Newbie</span>
              </li>
              <li class="line l--50">
                <span class="line__label">Geek</span>
              </li>
              <li class="line l--75">
                <span class="line__label">Ninja</span>
              </li>
              <li class="line l--100">
                <span class="line__label">Jedi</span>
              </li>
            </ul>

            <div class="charts">
              <div class="chart chart--dev">
                <span class="chart__title">Development</span>
                <ul class="chart--horiz">
                  <li class="chart__bar" style={{width: "85%"}}>
                    <span class="chart__label">Java</span>
                  </li>
                  <li class="chart__bar" style={{ width: "80%" }}>
                    <span class="chart__label">Spring</span>
                  </li>
                  <li class="chart__bar" style={{ width: "70%" }}>
                    <span class="chart__label">JavaScript</span>
                  </li>
                  <li class="chart__bar" style={{ width: "65%" }}>
                    <span class="chart__label">Python</span>
                  </li>
                  <li class="chart__bar" style={{ width: "70%" }}>
                    <span class="chart__label">Unit Testing</span>
                  </li>
                  <li class="chart__bar" style={{ width: "70%" }}>
                    <span class="chart__label">NodeJS</span>
                  </li>
                  <li class="chart__bar" style={{ width: "90%" }}>
                    <span class="chart__label">Algorithmic coding</span>
                  </li>
                  <li class="chart__bar" style={{ width: "65%" }}>
                    <span class="chart__label">Jenkins / (CI/CD)</span>
                  </li>
                  <li class="chart__bar" style={{ width: "85%" }}>
                    <span class="chart__label">Cloud (AWS, GCP)</span>
                  </li>
                  <li class="chart__bar" style={{ width: "85%" }}>
                    <span class="chart__label">MySQL</span>
                  </li>
                  <li class="chart__bar" style={{ width: "65%" }}>
                    <span class="chart__label">MongoDB</span>
                  </li>
                  <li class="chart__bar" style={{ width: "50%" }}>
                    <span class="chart__label">Docker/Kubernetes</span>
                  </li>
                </ul>
              </div>

              <div class="chart chart--prod">
                <span class="chart__title">Productivity</span>
                <ul class="chart--horiz">
                  <li class="chart__bar" style={{ width: "80%" }}>
                    <span class="chart__label">
                      Git + Github, TortoiseGit
                    </span>
                  </li>
                  <li class="chart__bar" style={{ width: "70%" }}>
                    <span class="chart__label">Microsoft Office</span>
                  </li>
                </ul>
              </div>

              <div class="chart chart--design">
                <span class="chart__title">Design</span>
                <ul class="chart--horiz">
                  <li class="chart__bar" style={{ width: "80%" }}>
                    <span class="chart__label">ReactJS</span>
                  </li>
                  <li class="chart__bar" style={{ width: "60%" }}>
                    <span class="chart__label">AngularJS</span>
                  </li>
                  <li class="chart__bar" style={{ width: "70%" }}>
                    <span class="chart__label">AXURE</span>
                  </li>
                  <li class="chart__bar" style={{ width: "70%" }}>
                    <span class="chart__label">HTML/CSS</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div class="card">
          <br />
          <p>
          <ul class="b">
            <li class="x">
            Experienced Software Developer with a demonstrated history of working in
            the software development industry
            </li>
            <br />
            <li class="x">
            I currently am working as Software Engineer Intern at <strong>Empirix, MA, USA</strong>
            </li>
            <br />
            <li class="x">
            I have experience in INDIA for about 1.5 year, Worked as Software Developer
              in Sapient Global Market, Banglore - The Logic Factory India, Ahmedabad
            </li>
            <br />
            <li class="x">
            Hobbies: Learn new stuff, Watching movies/series, talking to friend, sleeping and Algorithmic coding
            </li>
            <br />
            <li style={{fontStyle:'italic', fontSize:"20px"}}>
            Looking for:
            <br/>Full stack development role, <br/>Backend developer, <br/>Front-end Developer, <br/>Software Tester
            </li>
            <br />
            <li class="x">
              Hit me up at dd399@njit.edu / dhwaniljames@gmail.com for anything
            </li>
            <br />
            <li style={{fontSize:"15px"}} >
              If you are reading till here then, Thank you so much
            </li>
            </ul>
            </p>
        </div>
      </div>
    </body>
  );
}

export default AboutPage;